import * as React from 'react';
import { styled, useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import MuiDrawer from '@mui/material/Drawer';
import MuiAppBar from '@mui/material/AppBar';
import Toolbar from '@mui/material/Toolbar';
import List from '@mui/material/List';
import CssBaseline from '@mui/material/CssBaseline';
import Typography from '@mui/material/Typography';
import Divider from '@mui/material/Divider';
import IconButton from '@mui/material/IconButton';
import MenuIcon from '@mui/icons-material/Menu';
import ChevronLeftIcon from '@mui/icons-material/ChevronLeft';
import ChevronRightIcon from '@mui/icons-material/ChevronRight';
import ListItemButton from '@mui/material/ListItemButton';
import ListItemText from '@mui/material/ListItemText';

import {AcUnitOutlined, BlurOnOutlined, ErrorOutline, MenuBookOutlined, MicOffOutlined, PanToolOutlined, RestartAltRounded,
    ScatterPlotOutlined, SmokeFreeOutlined, SpeakerPhoneOutlined, ExpandMoreRounded} from "@mui/icons-material";
import {Accordion, ListSubheader, AccordionSummary, AccordionDetails, SvgIcon} from "@mui/material";

import {ghost_details} from "./Ghosts";
import * as ghosts from "./Ghosts";

import {ReactComponent as Dots_2} from "./assets/image2vector.svg";

const drawerWidth = 240;

const openedMixin = (theme) => ({
    width: drawerWidth,
    transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.enteringScreen,
    }),
    overflowX: 'hidden',
});

const closedMixin = (theme) => ({
    transition: theme.transitions.create('width', {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
    overflowX: 'hidden',
    width: `calc(${theme.spacing(7)} + 1px)`,
    [theme.breakpoints.up('sm')]: {
        width: `calc(${theme.spacing(8)} + 1px)`,
    },
});

const DrawerHeader = styled('div')(({ theme }) => ({
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'flex-end',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
}));

const AppBar = styled(MuiAppBar, {
    shouldForwardProp: (prop) => prop !== 'open',
})(({ theme, open }) => ({
    zIndex: theme.zIndex.drawer + 1,
    transition: theme.transitions.create(['width', 'margin'], {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
    }),
    ...(open && {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    }),
}));

const Drawer = styled(MuiDrawer, { shouldForwardProp: (prop) => prop !== 'open' })(
    ({ theme, open }) => ({
        width: drawerWidth,
        flexShrink: 0,
        whiteSpace: 'nowrap',
        boxSizing: 'border-box',
        ...(open && {
            ...openedMixin(theme),
            '& .MuiDrawer-paper': openedMixin(theme),
        }),
        ...(!open && {
            ...closedMixin(theme),
            '& .MuiDrawer-paper': closedMixin(theme),
        }),
    }),
);




let ghost_list = [];
let collected_evidence = [];

export function init_ghost_hunt() {
    ghost_list = (ghosts.get_all_ghosts());
}

export function manage_evidence_collection(evidence_id) {
    if (collected_evidence.includes(evidence_id)) {
        const index = collected_evidence.indexOf(evidence_id);
        collected_evidence.splice(index, 1);
    }
    else {
        collected_evidence.push(evidence_id);
    }
    console.clear();
    console.log("collected evidence: " + collected_evidence + "\ncollected_evidence.length: " + collected_evidence.length);
    ghost_list.forEach(function (ghost) {
        ghost.possibility = 0;
        collected_evidence.forEach(function (evidence) {
            if (ghost.evidence.includes(evidence)) {
                ghost.possibility++;
            }
        });
        console.log(ghost.name + ": " + ghost.possibility);
    });
}

export default function MiniDrawer() {
    init_ghost_hunt();
    const theme = useTheme();
    const [open, setOpen] = React.useState(false);

    const handleDrawerOpen = () => {
        setOpen(true);
    };

    const handleDrawerClose = () => {
        setOpen(false);
    };

    const [expanded, setExpanded] = React.useState(false);
    const handleAccordionChange = (panel) => (event, isExpanded) => {
        setExpanded(isExpanded ? panel : false);
    };

    const [chance, setChance] = React.useState(0);
    const [selected, setSelected] = React.useState("action");
    const handleEvidenceSelection = (ev_id) => {
        manage_evidence_collection(ev_id);
        setChance(collected_evidence.length);
        setSelected(selected === "success" ? "action" : "success");
    };

    const handleReset = () => {
        window.location.reload();
    }

    return (
        <Box sx={{ display: 'flex' }}>
            <CssBaseline />
            <AppBar position="fixed" open={open}>
                <Toolbar>
                    <IconButton
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        edge="start"
                        sx={{
                            marginRight: 5,
                            ...(open && { display: 'none' }),
                        }}>
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" noWrap component="div">
                        Quick Hunt
                    </Typography>
                </Toolbar>
            </AppBar>
            <Drawer variant="permanent" open={open}>
                <DrawerHeader>
                    <IconButton onClick={handleDrawerClose}>
                        {theme.direction === 'rtl' ? <ChevronRightIcon /> : <ChevronLeftIcon />}
                    </IconButton>
                </DrawerHeader>
                <Divider />
                <List
                    subheader={
                        <ListSubheader component="div" id="nested-list-subheader">
                            {open ?  'Evidences' : 'EV'}
                        </ListSubheader>
                    }>

                    <ListItemButton
                        onClick={() => handleEvidenceSelection( 0)}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <PanToolOutlined color={selected}
                            sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Fingerprints"/>
                    </ListItemButton>

                    <ListItemButton
                        onClick={() => handleEvidenceSelection( 1)}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <AcUnitOutlined color={selected}
                            sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Freezing Temperatures"/>
                    </ListItemButton>

                    <ListItemButton
                        onClick={() => handleEvidenceSelection( 2)}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <ScatterPlotOutlined color={selected}
                            sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Ghost Orbs"/>
                    </ListItemButton>

                    <ListItemButton
                        onClick={() => handleEvidenceSelection( 3)}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <SpeakerPhoneOutlined color={selected}
                            sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="EMF 5"/>
                    </ListItemButton>

                    <ListItemButton
                        onClick={() => handleEvidenceSelection( 4)}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <BlurOnOutlined color={selected} //"inherit" | "action" | "disabled" | "primary" | "secondary" | "error" | "info" | "success" | "warning",
                            sx={{
                            marginRight: 24,
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="D.O.T.S."/>
                    </ListItemButton>

                    <ListItemButton
                        onClick={() => handleEvidenceSelection( 5)}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <MenuBookOutlined color={selected}
                            sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Ghost Writings"/>
                    </ListItemButton>

                    <ListItemButton
                        onClick={() => handleEvidenceSelection( 6)}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <MicOffOutlined color={selected}
                            sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Spirit Box"/>
                    </ListItemButton>

                </List>
                <Divider/>
                <List
                    subheader={
                        <ListSubheader component="div" id="nested-list-subheader">
                            {open ?  'Tools' : 'T'}
                        </ListSubheader>
                    }>
                    <ListItemButton
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <SmokeFreeOutlined sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Smudge Timer"/>
                    </ListItemButton>
                    <Divider/>
                    <ListItemButton
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <ErrorOutline sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Nightmare Mode"/>
                    </ListItemButton>
                    <Divider/>
                    <ListItemButton
                        onClick={() => handleReset()}
                        sx={{
                            minHeight: 48,
                            justifyContent: open ? 'initial' : 'center',
                            px: 2.5,
                        }}>
                        <RestartAltRounded sx={{
                            minWidth: 0,
                            mr: open ? 3 : 'auto',
                            justifyContent: 'center',
                        }}/>
                        <ListItemText sx={{ opacity: open ? 1 : 0 }}  primary="Restart"/>
                    </ListItemButton>
                </List>
            </Drawer>

            <Box component="main" sx={{ flexGrow: 1, p: 0 }}>
                <DrawerHeader/>

                <div>
                    {ghost_list.map(function (ghost) {
                        // noinspection JSValidateTypes
                        return (
                            <Accordion expanded={expanded === "panel"+(ghost.id)} hidden={!(chance === ghost.possibility)} onChange={handleAccordionChange("panel"+(ghost.id))}>
                                <AccordionSummary
                                    expandIcon={<ExpandMoreRounded />}
                                    aria-controls="panel1bh-content"
                                    id="panel1bh-header">

                                    <Typography sx={{ flexShrink: 0, minWidth: 100, textAlign: 'left' }}>{ghost.name}</Typography>
                                    <Typography sx={{ flexGrow:1, color: 'text.secondary', textAlign: 'center'}}>
                                        {ghost.evidences_icons}
                                    </Typography>
                                </AccordionSummary>

                                <AccordionDetails>
                                    {ghost_details(ghost).map(function(detail, idx){
                                        return (
                                            <div key={idx}>
                                                {detail}
                                            </div>
                                        )
                                    })}
                                </AccordionDetails>
                            </Accordion>
                        )
                    })}
                </div>

            </Box>
        </Box>
    );
}
